<template>
  <div class="page-register">
    <div class="header">
      <header>
        <a href="#" class="site-logo"></a>
        <div class="login">
          <span></span>
          <router-link :to="{name:'login'}"></router-link>
        </div>
      </header>
    </div>

    <div class="content">
      <el-form
        :model="registerForm"
        status-icon
        :rules="rules"
        ref="registerForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="userName">
          <el-input type="text" v-model="registerForm.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="registerForm.password" autocomplete="off" @input="input"></el-input>
          <div class="pw-strength">
            <div :class="['bar', strengthClass]"></div>
            <div class="letter">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="" prop="rePassword">
          <el-input type="password" v-model="registerForm.rePassword" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('registerForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <footer></footer>
  </div>
</template>
<script>
import api from '@/api/index.js';
export default {
  data() {
    var validateUser = (rule, value, callback) => {
        console.log(value)
      if (value === "") {
        callback(new Error(""));
      }else if(value.length < 4 || value.length > 16){
          callback(new Error("4-16"));
      }else{
          callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.registerForm.rePassword !== "") {
          this.$refs.registerForm.validateField("rePassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    console.log(validateUser);
    console.log("111",validatePass)
    console.log("222",validatePass2)
    return {
      registerForm: {
        userName: "",
        password: "",
        rePassword: ""
      },
      strengthClass: "",
      rules: {
        userName: [{ validator: validateUser, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
        rePassword: [{ validator: validatePass2, trigger: "blur" }]

        }
    };
  },
  methods: {
    submitForm(formName) {
        console.log("")
        console.log("@@@@",formName);
        console.log("~~~~~~~~~",this.$refs[formName]);
        this.$refs[formName].validate((valid) => {
          console.log("!!!!!!!",valid);
          if (valid) {
              api.register({
                  params:this.registerForm
              }).then(res =>{
                  if(res.data.status == "success"){
                      this.$router.push({name:'login'});
                  }else{
                      alert(res.data.msg);
                  }
              })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    input(){
        var regStr = /(\w)+/g;
        var regNum  = /(\d)+/g;
        var reg = /_/g;
        var strongth = this.registerForm.password.match(reg) && this.registerForm.password.match(regStr) && this.registerForm.password.match(reg);
        if(this.registerForm.password.length > 20 || this.registerForm.password.length > 6 && strongth){
            this.strengthClass = "strong";
        }else if(this.registerForm.password.length  < 6){
            this.strengthClass = "week";
        }else if(!this.registerForm.password){
            this.strengthClass = "";
        }else{
            this.strengthClass = "normal";
        }

    }
  }
};
</script>
<style lang="scss">
@import "@/assets/css/register/index.scss";
</style>


