<template>
  <div class="m-product-categroy">
      <dl class="classic">
          <dt></dt>
          <dt></dt>
          <dd v-for="(item) in classicList" :key="item.type">
              <m-select :name="item.title" :list="item.sub_list"/>
          </dd>
      </dl>
      <dl class="classic">
          <dt></dt>
          <dt></dt>
          <dd v-for="(item) in areaList" :key="item.type">
              <m-select :name="item.title" :list="item.sub_list"/>
          </dd>
      </dl>
  </div>
</template>
<script>
import MSelect from './select.vue'
export default {
  data() {
    return {
      classicList: [
        {
          title: "",
          type: "food",
          sub_list: [
            {
              name: "",
              id: "Japan"
            },
            {
              name: "",
              id: "drink"
            },
            {
              name: "",
              id: "cate"
            }
          ]
        },
        {
          title: "",
          type: "hotal",
          sub_list: [
            {
              name: "",
              id: "hot_spring"
            },
            {
              name: "",
              id: "economy"
            },
            {
              name: "",
              id: "high"
            }
          ]
        }
      ],
      areaList: [
        {
          title: "",
          sub_list: [
            {
              name: "",
              id: 120000
            },

            {
              name: "",
              id: 12222
            }
          ]
        }
      ]
    };
  },
  components:{
      MSelect
  }
};
</script>


